/* Arrow */

.arrow {
	position: relative;
	width: 16px;
	height: 16px;
	background-color: #d9d9d9;
	border-radius: 50%;

	svg {
		position: absolute;
		top: 40%;
		left: 30%;
		width: 6px;
		height: 4px;

		stroke: #717171;
	}
}

.option--active .arrow {
	transform: rotate(-180deg);
	background-color: #aea1a1;

	svg {
		stroke: $white;
	}
}
