/* Header */
.header {
	padding-top: 40px;
	padding-bottom: 42px;
	background-color: $lbg;
	font-family: $Inika;
	font-weight: 400;
	font-size: 16px;

	& .container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 60px;
	}

	// Tablet
	@media (max-width: 1221px) {
		padding-top: 35px;
		padding-bottom: 35px;
	}

	// Mobile
	@media (max-width: 700px) {
		padding-bottom: 40px;
	}

	// Mobile XS
	@media (max-width: 380px) {
		padding-bottom: 16px;
	}
}

.logo {
	position: relative;
	text-align: right;
	text-decoration: none;
	color: $black;

	outline: 0;
	transition: all 0.3s ease-in;

	svg {
		margin-right: -3px;
		height: 22px;
		width: 22px;

		vertical-align: text-bottom;
		stroke: $green;
		fill: none;
	}

	&:hover,
	&:focus {
		font-weight: 700;
	}
}

.nav {
	z-index: 10;
	position: relative;
	font-family: $Inika;
	display: flex;

	a {
		margin-right: 46px;
		text-decoration: none;
		color: $links-nav;

		outline: 0;
		transition: all 0.3s linear;

		&:hover,
		&:focus {
			color: $orange;
			font-weight: 700;
		}
		&:last-child {
			margin-right: 0;
		}
	}

	// Mobile
	@media (max-width: 700px) {
		display: none;
		position: fixed;
		top: -100%;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(214, 231, 210, 0.95);
		opacity: 0;

		flex-direction: column;
		justify-content: center;
		align-items: center;

		transition: opacity 0.2s ease-in, top 0s linear 0.2s;

		a {
			margin-right: 0;
			margin-top: 30px;
			font-size: 28px;
			color: $green;

			&:first-child {
				margin-top: 0;
			}
		}

		&.nav--active {
			display: flex;
			top: 0;
			opacity: 1;
			transition: top 0s linear, opacity 0.2s ease-in;
		}
	}
}
