/* Footer */

.footer {
	padding-top: 40px;
	padding-bottom: 40px;
	display: flex;
	justify-content: space-between;
	align-items: center;

	font-size: 20px;
	line-height: 2.5;
	color: $black;

	> p {
		margin-left: 21px;
	}

	// Tablet
	@media (max-width: 1221px) {
		flex-direction: column;
		line-height: 1.75;

		.social {
			margin-top: 20px;
		}
	}

	// Mobile
	@media (max-width: 768px) {
		padding-top: 24px;
		padding-bottom: 18px;

		p {
			margin-left: 0;
			margin-top: 4px;
		}
	}

	// Mobile XS
	@media (max-width: 380px) {
		padding-top: 13px;

		.social {
			margin-top: 6px;
		}
	}
}

.copy {
	display: flex;
	gap: 20px;

	a {
		color: $black;
		text-decoration: none;
		outline: none;
		transition: 0.3s linear;

		&:hover {
			color: $green;
		}

		&:focus {
			color: $green;
			text-decoration: underline;
		}
	}
}

.social {
	display: flex;
	align-items: center;

	a {
		display: inline-block;
		height: 32px;
		width: 32px;

		outline: none;
		transition: all 0.3s linear;

		&:focus {
			background-color: rgba(214, 231, 210, 0.8);
			border-radius: 30%;
		}
	}

	a + a {
		margin-left: 20px;
	}

	svg {
		height: inherit;
		width: inherit;
		fill: $black;

		border: none;
		transition: 0.3s linear;

		&:hover {
			fill: $green;
		}
	}
}
