/* Section Service */

.service {
	padding-top: 40px;
	padding-bottom: 119px;
	background-color: $lbg;

	// Tablet
	@media (max-width: 1221px) {
		padding-top: 60px;
		padding-bottom: 100px;
	}

	// Mobile
	@media (max-width: 768px) {
		padding-bottom: 68px;
	}

	// Mobile XS
	@media (max-width: 380px) {
		padding-top: 30px;
		padding-bottom: 25px;
	}
}

.service__header {
	margin-bottom: 80px;

	display: flex;
	align-items: center;
	gap: 158px;

	.section-title {
		color: $green;

		// Tablet
		@media (max-width: 1221px) {
			margin-bottom: 42px;
		}

		// Mobile
		@media (max-width: 700px) {
			margin-bottom: 39px;
		}
	}

	// Tablet
	@media (max-width: 1221px) {
		display: block;
	}

	// Mobile
	@media (max-width: 768px) {
		margin-bottom: 40px;
	}
}

.service__filter {
	display: flex;
	flex-wrap: wrap;
	gap: 38px;

	// Mobile
	@media (max-width: 699px) {
		gap: 10px;
		justify-content: center;
	}

	// Mobile XS
	@media (max-width: 380px) {
		justify-content: flex-start;
	}
}

.service__gallery {
	display: flex;
	flex-wrap: wrap;
	row-gap: 53px;
	column-gap: 30px;
	justify-content: space-between;

	// Tablet
	@media (max-width: 1221px) {
		column-gap: 10px;
		row-gap: 35px;
	}

	// Mobile
	@media (max-width: 699px) {
		justify-content: center;
		row-gap: 32px;
	}
}

.card {
	width: 330px;
	text-align: center;
	transition: all 0.3s ease-in-out;

	img {
		border-radius: 20px 20px 0px 0px;
	}

	&--blur {
		filter: blur(2px);
	}

	// Tablet
	@media (max-width: 1221px) {
		width: 280px;
	}

	// Mobile
	@media (max-width: 768px) {
		width: 42%;

		img {
			object-fit: cover;
			height: 350px;
		}
	}

	// Mobile XS
	@media (max-width: 599px) {
		width: auto;
		height: auto;
	}

	// Mobile XS
	@media (max-width: 380px) {
		width: 276px;
	}
}

.card__content {
	padding: 8px 30px 36px;
	border: 1px solid $border;
	border-radius: 0px 0px 20px 20px;

	h3 {
		margin-bottom: 12px;
		font-family: $Inter;
		font-weight: 700;
		font-size: 20px;
		color: $orange;
	}

	// Tablet
	@media (max-width: 1221px) {
		padding-bottom: 20px;

		h3 {
			margin-bottom: 8px;
		}
	}
}

.button-filter {
	display: block;
	width: 161px;
	padding: 12px 26px;

	border: 2px solid $orange;
	background-color: transparent;
	box-shadow: 0 0px 0px $orange;

	font-family: $Inika;
	color: $orange;
	font-size: 20px;
	cursor: pointer;

	// Mobile
	@media (max-width: 699px) {
		padding: 5px 26px;
		width: 121px;
		font-size: inherit;
	}
}

.button-filter--active {
	background-color: $orange;
	color: $white;
}
