/* Section About */

.about {
	padding-top: 161px;
	padding-bottom: 94px;

	display: grid;
	grid-template-columns: 605px 464px;
	justify-content: space-between;
	align-items: flex-start;

	// Tablet
	@media (max-width: 1221px) {
		padding-top: 100px;
		padding-bottom: 100px;
		grid-template-columns: 55% 38%;
		align-items: center;
	}

	// Mobile
	@media (max-width: 768px) {
		padding-top: 40px;
		padding-bottom: 95px;
		display: block;
	}

	// Mobile
	@media (max-width: 700px) {
		padding-top: 30px;
		padding-bottom: 27px;
	}
}

.about__content {
	// Mobile
	@media (max-width: 768px) {
		margin-bottom: 68px;
	}

	// Mobile
	@media (max-width: 700px) {
		margin-bottom: 0;
	}

	.section-title {
		margin-bottom: 40px;
		width: 470px;
		color: $green;

		span {
			color: $orange;
		}

		// Mobile
		@media (max-width: 768px) {
			margin-bottom: 52px;
			margin-left: auto;
			margin-right: auto;
			width: 380px;
			text-align: center;
		}

		// Mobile XS
		@media (max-width: 450px) {
			margin-bottom: 15px;
			width: auto;
		}
	}

	p {
		line-height: 1.5;

		// Mobile
		@media (max-width: 768px) {
			font-size: 20px;
		}

		// Mobile XS
		@media (max-width: 380px) {
			font-size: 15px;
			line-height: 1.67;
		}
	}
}

.about__img {
	margin-top: -68px;

	// Mobile
	@media (max-width: 768px) {
		margin: 0 auto;
		width: 250px;
	}

	// Mobile XS
	@media (max-width: 380px) {
		width: 150px;
	}
}
