/* Button */

.button {
	padding: 10px 26px;
	display: block;
	border: 2px solid $orange;
	border-radius: 5px;
	background-color: transparent;

	font-family: $Inter;
	font-size: 16px;
	color: #000000;

	cursor: pointer;
	outline: 0;
	transition: 0.3s linear;

	&:hover {
		background: $orange;
		color: $white;
	}

	&:focus {
		transform: translateY(-5px);
		box-shadow: 0 10px 20px $orange;
	}

	// Mobile
	@media (max-width: 768px) {
		font-size: 15px;
	}
}
