/* Section Price */

.price {
	padding-top: 38px;
	padding-bottom: 112px;

	display: grid;
	grid-template-columns: 332px 452px;
	justify-content: space-between;

	// Tablet
	@media (max-width: 1221px) {
		padding-bottom: 95px;
		grid-template-columns: 332px 400px;
	}

	// Tablet
	@media (max-width: 900px) {
		grid-template-columns: 332px 350px;
	}

	// Mobile
	@media (max-width: 768px) {
		grid-template-columns: 332px 296px;
	}

	// Mobile
	@media (max-width: 750px) {
		grid-template-columns: 332px 220px;
	}

	// Mobile
	@media (max-width: 699px) {
		padding-top: 25px;
		padding-bottom: 34px;
	}

	// Mobile
	@media (max-width: 620px) {
		display: block;
	}

	// Mobile XS
	@media (max-width: 380px) {
		padding-bottom: 40px;
	}
}

.price__title,
.price__subtitle {
	font-family: "Inter";
	font-style: normal;
	font-weight: 700;
	font-size: 40px;
	line-height: 1.25;
	color: $black;

	// Tablet
	@media (max-width: 1221px) {
		font-size: 35px;
	}

	// Mobile
	@media (max-width: 699px) {
		font-size: 25px;
		text-align: center;
	}
}

.price__title {
	margin-bottom: 60px;

	// Mobile
	@media (max-width: 768px) {
		margin-bottom: 57px;
	}

	// Mobile XS
	@media (max-width: 380px) {
		margin-bottom: 36px;
	}
}

.price__options {
	padding: 32px 41px;
	height: 282px;

	display: grid;
	align-items: center;
	justify-content: center;
	gap: 32px;

	border: 1px solid $border;
	border-radius: 20px;

	transition: all 0.3s ease-in;

	// Mobile
	@media (max-width: 699px) {
		margin-left: auto;
		margin-right: auto;
		padding: 29px 22px;
	}

	// Mobile XS
	@media (max-width: 380px) {
		padding: 28px 41px;
		width: 224px;
		height: 235px;
		gap: 13px;
	}
}

.option {
	width: 250px;
	position: relative;

	background: $lbg;
	border-radius: 20px;
	border: none;

	opacity: 1;
	cursor: pointer;
	transition: all 0.3s ease-in-out;

	&:hover {
		box-shadow: 0px 5px 5px 2px rgba(0, 0, 0, 0.1);
		background: $lgreen;
	}

	button:focus {
		background: $lgreen;
	}

	&--active {
		box-shadow: 0px 5px 5px 2px rgba(0, 0, 0, 0.1);
		background: $lgreen;
	}

	// Mobile
	@media (max-width: 699px) {
		display: block;
	}

	// Mobile XS
	@media (max-width: 380px) {
		width: auto;
	}

	.hidden {
		opacity: 0;
		transition: all 0.3s ease-in-out;
	}
}

.option__header {
	position: relative;
	padding: 13px 40px;
	width: 250px;

	display: flex;
	justify-content: space-between;
	align-items: center;

	border-radius: 20px;
	border: none;
	background-color: transparent;

	text-align: left;
	font-weight: 700;
	font-size: 20px;
	color: $black;

	outline: none;

	// Mobile XS
	@media (max-width: 380px) {
		padding: 15px 30px;
		width: 180px;
		font-size: 15px;
		line-height: 1.33;
	}
}

.option__content {
	display: none;
	margin: 0 auto;
	padding-top: 6px;
	padding-bottom: 12px;
	width: 170px;
	border-top: 0.5px solid #aea1a1;

	font-weight: 300;
	font-size: 10px;
	color: $lgray;

	// Mobile XS
	@media (max-width: 380px) {
		padding: 15px 15px;
	}
}

.option--active {
	background: $lgreen;

	.option__content {
		display: block;
	}
}

.option__value {
	margin-top: 6px;
	margin-bottom: 12px;
	font-weight: 400;
	font-size: 12px;
	line-height: 1.67;
	color: $black;

	span {
		color: $orange;
		font-weight: 700;
	}
}

.option__cost {
	margin-left: -3px;
	font-size: 16px;
}

.option__order {
	padding: 3px 20px;
	background: $white;
	border-radius: 10px;
	border: none;

	font-weight: 700;
	font-size: 14px;
	color: $lgray;
	text-decoration: none;

	outline: none;
	transition: 0.3s linear;

	&:hover,
	&:focus {
		box-shadow: 3px 3px 8px 0.3px #6d6e6d57;
		color: $green;
	}
}

.price__wrap {
	text-align: right;

	* + * {
		margin-top: 39px;
	}

	// Tablet
	@media (max-width: 1221px) {
		* + * {
			margin-top: 25px;
		}
	}

	// Mobile
	@media (max-width: 699px) {
		width: 230px;

		* + * {
			margin-top: 22px;
		}
	}

	// Mobile
	@media (max-width: 620px) {
		text-align: center;
		margin: auto;
	}

	// Mobile XS
	@media (max-width: 380px) {
		* + * {
			margin-top: 20px;
		}
	}
}

.price__subtitle {
	span {
		color: $green;
	}

	// Tablet
	@media (max-width: 900px) {
		margin-top: 102px;
		font-size: 25px;
		line-height: 1.4;
	}

	// Mobile
	@media (max-width: 699px) {
		margin-top: 19px;
		font-size: 20px;
		line-height: 1.75;
	}

	// Mobile XS
	@media (max-width: 380px) {
		margin-top: 21px;
	}
}

.price__button {
	display: inline-block;
	padding: 10px 30px;

	font-family: "Inika";
	font-weight: 700;
	font-size: 20px;
	text-decoration: none;

	// Mobile
	@media (max-width: 699px) {
		font-size: 15px;
		padding: 8px 22px;
	}

	// Mobile XS
	@media (max-width: 380px) {
		padding: 6px 23px;
	}
}

.price__img {
	margin-left: auto;
	width: 180px;
	height: 180px;
	margin-right: -20px;

	// Mobile
	@media (max-width: 699px) {
		margin-right: 0;
	}

	// Mobile
	@media (max-width: 620px) {
		display: none;
	}
}
