/* Navigation Icons */
// Ширина и высота линии в иконке
$lineWidth: 40px;
$lineHeight: 5px;
$color: $lgray;

// Размеры контейнера
$width: $lineWidth;
$height: $lineHeight * 5;

.nav-icon {
	z-index: 11;
	width: $width * 1.5;
	height: $width * 1.5;
	border-radius: 50%;

	display: none;
	justify-content: center;
	align-items: center;
	background-color: transparent;
	outline: none;
	border: none;
	transition: all 0.3s ease-in-out;
	cursor: pointer;

	@media (max-width: 700px) {
		display: flex;
	}

	&:hover,
	&:focus {
		background-color: $white;
	}
}

.nav-icon__middle {
	position: relative;
	width: $lineWidth;
	height: $lineHeight;
	background-color: $color;
	border-radius: 5px;

	&::before,
	&::after {
		content: "";
		position: absolute;
		left: 0;
		width: $lineWidth;
		height: $lineHeight;
		transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
		background-color: $color;
		border-radius: 5px;
	}

	&::before {
		top: -($lineHeight * 2);
	}

	&::after {
		top: $lineHeight * 2;
	}
}

/* Active state */
.nav-icon.nav-icon--active {
	position: fixed;
	top: 35px;
	right: 35px;

	// Mobile XS
	@media (max-width: 380px) {
		right: 15px;
	}
}

.nav-icon.nav-icon--active .nav-icon__middle {
	background-color: transparent;
	$color: $green;

	&::before,
	&::after {
		top: 0;
		transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
		background-color: $color;
	}

	&::before {
		transform: rotate(45deg);
	}

	&::after {
		transform: rotate(-45deg);
	}
}
