/* Section Welcome */

.welcome {
	padding-top: 59px;
	padding-bottom: 58px;
	background-color: $lbg;

	.container {
		display: grid;
		grid-template-columns: 448px 547px;
		justify-content: space-between;
		align-items: center;

		// Tablet
		@media (max-width: 1221px) {
			grid-template-columns: auto 320px;
			gap: 42px;
		}

		// Mobile
		@media (max-width: 768px) {
			grid-template-columns: 346px auto;
			gap: 10px;
		}

		// Mobile XS
		@media (max-width: 620px) {
			grid-template-columns: auto;
		}
	}

	// Tablet
	@media (max-width: 1221px) {
		padding-top: 34px;
		padding-bottom: 71px;
	}

	// Mobile
	@media (max-width: 700px) {
		padding-top: 26px;
		padding-bottom: 23px;
	}
}

.welcome__img {
	width: 448px;

	// Tablet
	@media (max-width: 1221px) {
		align-self: flex-start;
		order: 2;
		width: auto;
	}

	// Mobile
	@media (max-width: 768px) {
		width: 280px;
	}

	// Mobile
	@media (max-width: 620px) {
		display: none;
	}
}

.welcome__content {
	h1 {
		margin-bottom: 42px;

		font-family: $Inter;
		font-weight: 700;
		font-size: 45px;
		line-height: 1.11;
		color: $lgray;

		span {
			color: $green;
		}

		// Tablet
		@media (max-width: 1221px) {
			margin-bottom: 30px;
			font-size: 35px;
			line-height: 1.4;
		}

		// Mobile
		@media (max-width: 700px) {
			margin-bottom: 20px;
			font-size: 25px;
		}
	}

	p {
		line-height: 1.75;

		// Tablet
		@media (max-width: 1221px) {
			line-height: 1.56;
		}
	}
}

.welcome__button {
	display: inline-block;
	text-decoration: none;
	margin-top: 48px;

	// Tablet
	@media (max-width: 1221px) {
		margin-top: 37px;
		padding: 10px 29px;
	}

	// Mobile
	@media (max-width: 700px) {
		margin-top: 25px;
		font-size: 10px;
		padding: 10px 16px;
	}
}
