/* Section contact */

.contact {
	position: relative;
	padding-top: 60px;
	background-color: $lbg;

	// Mobile
	@media (max-width: 870px) {
		height: 739px;
		padding-top: 40px;
		padding-bottom: 0;
	}

	// Mobile XS
	@media (max-width: 380px) {
		height: auto;
		min-height: 445px;
		padding-top: 25px;
		padding-bottom: 33px;
	}
}

.contact__title {
	font-weight: 700;
	color: $green;
	text-align: center;

	// Tablet
	@media (max-width: 1221px) {
		margin-left: 130px;
	}

	// Mobile
	@media (max-width: 870px) {
		margin-left: 0;
		font-size: 40px;
	}

	// Mobile
	@media (max-width: 700px) {
		font-size: 25px;
	}
}

.contact__content {
	margin-top: 34px;
	display: flex;
	justify-content: space-between;

	// Mobile
	@media (max-width: 870px) {
		flex-direction: column-reverse;
		align-items: center;
		margin-top: 60px;
	}

	// Mobile
	@media (max-width: 700px) {
		margin-top: 82px;
	}
}

.contact__img {
	align-self: flex-end;
	margin-top: -93px;

	// Mobile
	@media (max-width: 870px) {
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);

		width: 200px;
		margin: auto;
		// margin-top: 15px;
		margin-bottom: auto;
	}

	// Mobile XS
	@media (max-width: 380px) {
		width: 100px;
	}
}

.select {
	width: 450px;
	transition: all 0.3s ease-in-out;

	// Tablet
	@media (max-width: 960px) {
		width: 400px;
	}

	// Mobile
	@media (max-width: 870px) {
		width: 450px;
		min-height: 284px;
	}

	// Mobile
	@media (max-width: 700px) {
		width: 100%;
		min-height: 167px;
	}

	// Mobile XS
	@media (max-width: 380px) {
		max-width: 300px;
	}
}

.contact__card-wrap {
	margin-top: 47px;

	// Mobile
	@media (max-width: 870px) {
		margin-top: 59px;
	}

	// Mobile XS
	@media (max-width: 380px) {
		margin-top: 49px;
	}
}

.select__button,
.select__list {
	width: 100%;
	padding: 0 34px 0 20px;

	display: flex;
	justify-content: space-between;
	align-items: center;

	background: $lgreen;
	border-radius: 0px;
	border: 1px solid transparent;
	box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

	text-align: left;
	font-weight: 400;
	font-size: 20px;
	line-height: 50px;
	letter-spacing: 0.05em;
	color: $lgray;

	cursor: pointer;

	// Tablet
	@media (max-width: 1221px) {
		font-size: 16px;
	}
}

.select__button {
	position: relative;
	outline: 0;
	transition: 0.3s linear;

	&:hover {
		box-shadow: 0px 5px 5px 2px rgba(0, 0, 0, 0.3);
	}

	&:focus,
	&--active {
		background: #c1e698;
		border: 1px solid $lgreen;

		span {
			background: #8ba07e;
		}

		svg {
			stroke: $white;
			transform: rotate(-180deg);
		}
	}

	&::after {
		content: "";
		width: 16px;
		height: 16px;
		background: #d9d9d9;
		border-radius: 50%;

		background-image: url("../img/arrow.svg");
		background-repeat: no-repeat;
		background-position: center;
	}

	// Mobile XS
	@media (max-width: 380px) {
		&::after {
			margin-right: -14px;
		}
	}
}

.select__button--active {
	&.select__button::after {
		background-image: url("../img/arrow-active.svg");
		transform: rotate(-180deg);
		background-color: #aea1a1;
	}
}

.select__list {
	padding: 25px 26px;
	display: grid;
	grid-template-columns: 100%;
	gap: 6px;
	opacity: 1;
	transition: opacity 0.5s ease-in-out;
}

.select__list-item {
	background-color: transparent;
	border: none;
	border-bottom: 1px solid #d9d9d9;

	text-align: left;
	font-size: 16px;
	line-height: 2.5;
	letter-spacing: 0.05em;
	color: $green;
	outline: 0;
	transition: all 0.3s ease-in-out;

	&:hover,
	&:focus {
		color: $lgray;
		border-bottom: 1px solid $lgray;
	}
}

.contact__card {
	margin: auto;
	padding: 32px;
	padding-top: 17px;
	width: 450px;

	background: $lbg;
	border: 1px solid $black;
	border-radius: 20px;
	transition: opacity 0.3s ease-in-out;

	// Tablet
	@media (max-width: 960px) {
		padding: 12px 33px 31px;
		width: 400px;
	}

	// Mobile
	@media (max-width: 870px) {
		width: 450px;
	}

	// Mobile XS
	@media (max-width: 550px) {
		width: auto;

		td {
			padding-top: 15px;
			line-height: 1.25;
		}
	}

	// Mobile XS
	@media (max-width: 340px) {
		padding: 12px 25px 31px;
	}
}

.contact__address {
	font-style: normal;
	font-weight: 400;
	font-size: 16px;
	line-height: 2.5;
	letter-spacing: 0.05em;
	color: $lgray;

	opacity: 1;
	transition: all 2s ease-in-out;

	a {
		text-decoration: none;
		color: $lgray;
		outline: none;
		transition: 0.3s linear;

		&:hover,
		&:focus {
			color: $green;
		}
	}

	// Table
	@media (max-width: 960px) {
		line-height: 2.37;
	}
}

.contact__item {
	padding-right: 15px;
	color: $black;

	// Mobile XS
	@media (max-width: 480px) {
		width: 68px;
		padding-right: 5px;
		line-height: 1;
	}
}

.contact__button-call {
	margin: 24px auto 0;
	padding: 10px 29px;
	width: 122px;
	display: block;

	text-align: center;
	text-decoration: none;

	font-family: "Inika";
	font-style: normal;
	font-weight: 700;
	font-size: 10px;
	color: $lgray;

	// Table
	@media (max-width: 960px) {
		margin: 14px auto 0;
		padding: 5px 23px;
		width: 124px;
	}
}
