/* Reset and base styles  */
*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	height: 100%; 
	scroll-behavior: smooth;
}

body {
	height: 100%; 
	display: flex; 
	flex-direction: column; 
	margin: 0;
	font-family: $Inter;
	color: $lgray;
}

body > * {
	flex-shrink: 0; 
}

input,
textarea,
button {
	font-family: inherit;
	padding: 0;
}

label {
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

ul {
	margin: 0;
	padding: 0;
	list-style-type: none;
}

fieldset {
	border: none;
	margin: 0;
	padding: 0;
}

legend {
	display: block;
	margin: 0;
	padding: 0;
	border: none;
}

/* General styles */

.container {
	width: 1250px;
	margin: 0 auto;
	padding-left: 15px;
	padding-right: 15px;

	// Tablet
	@media (max-width: 1221px) {
		width: auto;
		padding-left: 35px;
		padding-right: 45px;
	}

	// Mobile
	@media (max-width: 768px) {
		width: auto;
	}

	// Mobile
	@media (max-width: 699px) {
		padding-right: 35px;

	}

	// Mobile XS
	@media (max-width: 380px) {
		padding-left: 23px;
    	padding-right: 13px;
	}
}

.section-text {
	font-size: 20px;
	color: $dgray;

	// Mobile
	@media (max-width: 768px) {
	font-size: 16px;
	}
}

.section-title {
	font-family: $Inika;
	font-weight: 400;
	font-size: 40px;

	// Tablet
	@media (max-width: 1221px) {
		font-size: 35px;
	}

	// Mobile
	@media (max-width: 699px) {
		font-size: 25px;
	}
}

/* Utility */

.hidden {
	display: none !important;
	opacity: 0;
}

/* Sticky Footer */

.footer {
	margin-top: auto;
}
